<template>
    <div id="rightSideNav">
        <div id="logoWrap"><router-link to="/login">
            <img id="logo" :src="logoSrc" alt="myself" width="130px"></router-link>
        </div>
        <div id="mySocialMedia">
            <div class="iconsWrap"><a href="https://github.com/satanruiko/" alt="Github" target="_blank">
                <img src="../assets/github.png" width="35px" height="35px"></a>
            </div>
            <div class="iconsWrap"><a href="https://coolapk.com/" alt="Coolapk" target="_blank">
                <img src="../assets/coolapk.png" width="35px" height="35px"></a>
            </div>
            <div class="iconsWrap"><a href="https://space.bilibili.com/179116718/" alt="Bilibili" target="_blank">
                <img src="../assets/bilibili.png" width="35px" height="35px"></a>
            </div>
        </div>
        <div id="someInformation">
            <h4>一起来玩耍吧</h4>
        </div>
    </div>
</template>

<script>
export default {
    name:'rightSideNav',
    data(){
        return{
            logoSrc:"static/icons/0.ico"
        }
    },
    mounted(){
        setTimeout(this.changeLogo,27000)
    },
    methods:{
        changeLogo(){
            var logoNum=Math.floor(Math.random()*6)
            this.logoSrc=`static/icons/${logoNum}.ico`
            setTimeout(this.changeLogo,27000)
        }
    }
    
}
</script>

<style scoped>
#rightSideNav {
    width: 15vw;
    height: 100vh;
    position: fixed;
    right: 0;
    top: 0;
    background-color:#7db9de ;
    box-shadow: -2px 0 6px 0 #333333;
}

#logoWrap {
    width: 130px;
    margin: 5px auto;
    border-bottom: 1px solid white;
}
#logo{
    border-radius: 50%;
    transition:0.4s;
}
#logo:hover{
    box-shadow: 0 0 20px whitesmoke;
}
#mySocialMedia{
    display: flex;
    justify-content: space-between;
    margin: 0.5em 0;
}
.iconsWrap {
    width: 32px;
    height: 32px;
    margin: 6px auto;
}

.iconsWrap img{
    transition: 0.5s;
}

.iconsWrap img:hover{
    transform: scale(1.2);
}
#someInformation{
    border-top: 1px solid white;
    width: 130px;
    margin: 0 auto;
    text-align: center;
}
</style>


